@import url(//stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
@import url(//fonts.googleapis.com/css?family=Saira+Extra+Condensed:100,200,300,400,500,600,700,800,900);
@import url(//cdnjs.cloudflare.com/ajax/libs/devicons/1.8.0/css/devicons.min.css);
 @media (min-width: 992px) {
    .cv-container {
        padding-top:0;
        padding-left: 17rem;
    }
}

.cv-container {
    padding-top: 0;
    color: #868e96;
}

h1 {
    font-size: 6rem;
    line-height: 5.5rem
}

h2 {
    font-size: 3.5rem
}

.subheading {
    text-transform: uppercase;
    font-weight: 500;
    font-family: 'Saira Extra Condensed',serif;
    font-size: 1.35rem
}

.list-social-icons a {
    color: #495057
}

.list-social-icons a:hover {
    color: #bd5d38
}

.list-social-icons a .fa-lg {
    font-size: 1.75rem
}

.list-icons {
    font-size: 3rem
}

.list-icons .list-inline-item i:hover {
    color: #bd5d38
}

#sideNav .navbar-nav .nav-item .nav-link {
    font-weight: 600;
    text-transform: uppercase
}

@media (min-width: 992px) {
    #sideNav {
        text-align:center;
        position: fixed;
        top: 0;
        left: 0;
        display: flex;
        flex-direction: column;
        width: 17rem;
        height: 100vh
    }

    #sideNav .navbar-brand {
        display: flex;
        margin: auto auto 0;
        padding: .5rem
    }

    #sideNav .navbar-brand .img-profile {
        max-width: 10rem;
        max-height: 10rem;
        border: .5rem solid rgba(255,255,255,.2)
    }

    #sideNav .navbar-collapse {
        display: flex;
        align-items: flex-start;
        flex-grow: 0;
        width: 100%;
        margin-bottom: auto
    }

    #sideNav .navbar-collapse .navbar-nav {
        flex-direction: column;
        width: 100%
    }

    #sideNav .navbar-collapse .navbar-nav .nav-item {
        display: block
    }

    #sideNav .navbar-collapse .navbar-nav .nav-item .nav-link {
        display: block
    }
}

section.resume-section {
    border-bottom: 1px solid #dee2e6;
    padding-top: 5rem!important;
    padding-bottom: 5rem!important
}

section.resume-section .resume-item .resume-date {
    min-width: none
}

@media (min-width: 768px) {
    section.resume-section {
        min-height:100vh
    }

    section.resume-section .resume-item .resume-date {
        min-width: 18rem
    }
}

@media (min-width: 992px) {
    section.resume-section {
        padding-top:3rem!important;
        padding-bottom: 3rem!important
    }
}

.h1--custom, .h2--custom,.h3--custom,h5,h6 {
  font-family: 'Saira Extra Condensed',serif;
  font-weight: 700;
  text-transform: uppercase;
  color: #343a40
}

.h1--custom {
  font-size: 6rem;
  line-height: 5.5rem
}

.h2--custom {
  font-size: 3.5rem;
}
.h3--custom {
  font-size: 1.75rem;
}


.bg-primary--custom {
    background-color: #bd5d38!important
}

.text-primary--custom  {
    color: #bd5d38!important;
}

.overview {
  padding: 0;
}
